export default {
  data() {
    return {
      show: false,
      cancelBtn:true,
      submitBtn:true,
      delTime:null,
      confirmModalOptions:{},
      defaultOptions:{
        title: "提示", // 弹窗标题
        message: "文字内容",  // 弹窗内容
        hideOnBlur: true, // 是否在点击遮罩时自动关闭弹窗
        btnSubmitText: "确定",  // 确认按钮的显示文字
        btnCancelText: "取消",  // 取消按钮的显示文字
        zIndex:1000,// 遮罩层 z-index 值 默认1000
        btnSubmitFunction: function () { // 确认按钮的返回函数
          console.log("确定")
        },
        btnCancelFunction: function () { // 取消按钮的返回函数
          console.log("取消")
        }
      }
    }
  },
  methods: {
    modalOptions (opt) {
      this.confirmModalOptions = opt ? opt : this.defaultOptions;
      this.showModel();
    },

    closeModel () {
      this.show = false;
      // this.deleteModel()
    },

    // deleteModel () {
    //   this.delTime = setTimeout(() => {
    //     document.body.removeChild(this.$refs.mask)
    //   },300)
    // },

    showModel () {
      this.show = true;
      document.body.appendChild(this.$refs.mask)
    },

    confirmCancel () {
      this.show = false;
      if(typeof (this.confirmModalOptions.btnCancelFunction)==='function'){
        this.confirmModalOptions.btnCancelFunction();
      }
      // this.deleteModel()
    },

    confirmSubmit () {
      this.show = false;
      if(typeof (this.confirmModalOptions.btnSubmitFunction)==='function'){
        this.confirmModalOptions.btnSubmitFunction();
      }
      // this.deleteModel()
    }
  },
  watch:{
    confirmModalOptions (newVal, oldVal) {
      typeof newVal.zIndex != "undefined" ? this.$refs.mask.style.zIndex = newVal.zIndex :this.$refs.mask.style.zIndex = this.defaultOptions.zIndex;
      newVal.btnCancelText ? this.cancelBtn = true : this.cancelBtn = false;
      newVal.btnSubmitText ? this.submitBtn = true : this.submitBtn = false;
    }
  }
}




