export default {
  name: 'HelloWorld',
  data () {
    return {
      confirmOptions:{}
    }
  },
  mounted(){

   
  },
  methods:{
    confirmBtn(){
      this.$_confirm({
        title: "提示", // 弹窗标题
        message: "文字内容",  // 弹窗内容
        hideOnBlur: true, // 是否在点击遮罩时自动关闭弹窗
        btnSubmitText: "确定",  // 确认按钮的显示文字
        btnCancelText: "取消",  // 取消按钮的显示文字
        zIndex:2100,// 遮罩层 z-index 值 默认1000
        btnSubmitFunction: function () { // 确认按钮的返回函数
          console.log("confirmBtn确定")
        },
        btnCancelFunction: function () { // 取消按钮的返回函数
          console.log("confirmBtn取消")
        }
      })
    },
    alertBtn(){
      this.$_confirm({
        title: "alert",
        message: "alert文字内容",
        hideOnBlur: false,
        btnSubmitText: "确定",
        btnSubmitFunction: function () {
          console.log("alertBtn确定")
        }
      })
    }
  }
}



