import Vue from 'vue'
import ConfirmComponent from './confirm.vue'
const MessageBoxConstructor = Vue.extend(ConfirmComponent);
let instance = new MessageBoxConstructor({
  el: document.createElement('div')
});
Vue.prototype.$_confirm = function(opt){
	instance.modalOptions(opt)
}


